@font-face {
  font-family: Alte Haas;
  font-style: normal;
  font-weight: normal;
  src: url("./assets/AlteHaasGroteskBold.ttf") format("trueType");
}

.video {
  position: relative;
}

.content {
  position: absolute;
  top: 0;
  font-family: Alte Haas, sans-serif;
  font-size: 3em;
  line-height: 0.1em;
  color: #fffff5;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  padding: 0;
}

.content h1 {
  text-shadow: 2px 2px 6px #00000079;
}
